<template>
  <div
    id="search"
    v-cloak
    my-index="precisionSearch"
  >
    <!-- g-view 是可视部分s -->
    <div v-if="false">
      <div
        class="g-view"
        style="top:60px;"
      >
        <div class="g-search-list">
          <div class="weui-cells">
            <a
              class="weui-cell weui-cell_access"
              href="javascript:;"
              @click="isShow(true)"
            >
              <div class="weui-cell__bd  ">
                <p>行业</p>
              </div>
              <span
                v-for="(value,index) in wordlistindustry"
                :key="index"
              >
                {{value.name}}
                <span>、</span>
              </span>
            </a>
            <a
              class="weui-cell weui-cell_access"
              @click="isShow(false)"
            >
              <div class="weui-cell__bd">
                <p>省份</p>
              </div>
              <div class="weui-cell__ft signalLine provide">
                <span
                  v-for="(value,index) in wordlistarea"
                  :key="index"
                >
                  {{value.name}}
                </span>
              </div>
            </a>
            <div class="weui-cell">
              <div class="weui-cell__hd"><label class="u-reset weui-label">主营产品/业务</label></div>
              <div class="weui-cell__bd">
                <input
                  class="u-reset weui-input"
                  placeholder="请输入主营产品/业务"
                  v-model="bussiness"
                />
              </div>
            </div>
            <div class="weui-cell">
              <div class="weui-cell__hd"><label class="u-reset weui-label">企业名称</label></div>
              <div class="weui-cell__bd">
                <input
                  class="u-reset weui-input"
                  placeholder="请输入企业名称或名称关键字"
                  v-model="keyname"
                />
              </div>
            </div>

            <a
              @click="capitalChoose"
              class="weui-cell weui-cell_access"
            >
              <div class="weui-cell__bd">
                <p>注册资金</p>
              </div>
              <div class="weui-cell__ft">{{capital}}</div>
            </a>
            <a
              @click="excludeChoose"
              class="weui-cell weui-cell_access"
              href="javascript:;"
            >
              <div class="weui-cell__bd">
                <p>排除营销</p>
              </div>
              <div class="weui-cell__ft">{{exclude}}</div>
            </a>
          </div>
        </div>
        <a
          style="width:100%;margin:0;border-radius:0;"
          @click="search"
          class=" u-reset weui-btn weui-btn_primary reset-search"
        >{{companyList.length ? '重新搜索': '搜索'}}</a>
      </div>
      <!-- <transition name="search-init"> -->
      <div
        class="resultList"
        v-if="companyList.length"
      >

        <div class="m-title">
          <span class="count">共{{count}}家企业</span>
          <span
            class="more"
            @click="getMore"
          >
            <i
              class="iconfont-search f-rotate"
              :style="deg"
            >&#xe60f;</i>
            下一页
          </span>
        </div>
        <!-- 有序列表 -->
        <div class="m-ordered-list">
          <div
            class="u-ordered-item"
            v-for="(company,index) in companyList"
            :key="index"
          >
            <span class="company-order">{{index+1}}、</span>
            <!-- 汉字显示13 大写英文20 小写英文25 -->
            <span class="company-name signalLine">{{company.enterprise[0]}}</span>&nbsp;
            <span class="company-tel">[{{company.mobile?company.mobile:'末填写'}}]</span>
          </div>
        </div>

      </div>
      <!-- </transition> -->

      <!-- 下方按钮组s -->

      <!-- 下方按钮组e -->
      <div
        id="loadingToast"
        :style="{display:isload}"
      >
        <div class="weui-mask_transparent"></div>
        <div class="weui-toast">
          <i class="weui-loading weui-icon_toast"></i>
          <p class="weui-toast__content">数据加载中</p>
        </div>
      </div>

      <flow-window
        v-if="flowWindow_name=='area'&&isShowFlowWindow"
        :title="flag ? '选择行业':'选择区域'"
        :idata="flag ? industry:area "
        v-on:receive="closeFlowWindowP"
        v-on:chagewordlist="showwordlist"
      ></flow-window>
    </div>

    <div id="precisionSearch">
      <div class="magnifierBox">
        <span class="magnifierOut">
          <span class="magnifierInner">
            <span></span>
          </span>
        </span>
      </div>
      <ul class="list">
        <li
          v-for="(item,index) in  thisPageData"
          :key="index"
          class="item"
          ref="li"
        >
          <span class="text">{{item}}</span>
          <span
            class="text r"
            @click="showTypeList(index,item)"
          >{{curSelect[index][index]}}</span>
        </li>
      </ul>
      <button
        class="searchBtn"
        @click="searchBtn"
      >搜索</button>
      <ul
        class="list sp"
        ref="hiddenList"
        v-if="isShowSelectList"
      >
        <li
          v-for="(item,index) in curDatalist"
          :key="index"
          class="item sp-i"
        >
          <span
            class="text"
            @click="select(item)"
          >{{item.name}}</span>
        </li>
      </ul>
      <div class="resultList">
        <div class="top">
          <span class="count">共{{count}}家企业</span>
          <span
            class="more"
            @click="getMore"
          >下一页</span>
        </div>
        <ul class="lista">
          <li
            class="itema"
            v-for="(item,index) in resultList"
            :key="index"
          >
            <span>{{index+1}}、</span>
            <span>{{item.enterprise[0]}}</span>
            <span> [{{item.mobile}}]</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import FlowWindow from "./FlowWindow";
var that = null;

export default {
  data() {
    return {
      postData: {},
      resultList: [], //保存搜索结果列表
      isShowSelectList: false, //是否显示下拉列表
      curDatalist: [],
      curIndex: 0,
      curSelect: [
        { 0: "" || "请选择" },
        { 1: "" || "请选择" },
        { 2: "" || "请选择" },
        { 3: "" || "不过滤" }
      ],
      thisPageData: ["行业", "省份", "主营产品/业务", "过滤已发送号码"],
      count: "" || "1万",
      private: "",
      isShowNum: 1,
      companyList: [],
      /** 动画部分s*/
      canRotate: true, //防抖开关
      newdeg: 0, //旋转计数
      deg: {
        transform: "rotate(0deg)"
      },
      isload: "none",
      /**动画部分e */
      bussiness: "", //主营产品/业务
      keyname: "", //企业名称

      // area: [], //省份列表
      wordlistarea: undefined, //已选中的省份
      wordlistindustry: undefined, //已选中的行业
      flowWindow_name: "area",
      isShowFlowWindow: false,
      capital: "",
      exclude: "",
      flag: false,
      industry: [
        { name: "农、林、牧、渔业", id: 1 },
        { name: "采矿业", id: 2 },
        { name: "制造业", id: 3 },
        { name: "电力、热力、燃气及水生产和供应业", id: 4 },
        { name: "建筑业", id: 5 },
        { name: "批发和零售业", id: 6 },
        { name: "交通运输、仓储和邮政业", id: 7 },
        { name: "住宿和餐饮业", id: 8 },
        { name: "信息传输、软件和信息技术服务业", id: 9 },
        { name: "金融业", id: 10 },
        { name: "房地产业", id: 11 },
        { name: "租赁和商务服务业", id: 12 },
        { name: "科学研究和技术服务业", id: 13 },
        { name: "水利、环境和公共设施管理业", id: 14 },
        { name: "居民服务、修理和其他服务业", id: 15 },
        { name: "教育", id: 16 },
        { name: "卫生和社会工作", id: 17 },
        { name: "文化、体育和娱乐业", id: 18 },
        { name: "公共管理、社会保障和社会组织", id: 19 },
        { name: "国际组织", id: 20 }
      ],
      area: [
        { name: "北京" },
        { name: "天津" },
        { name: "上海" },
        { name: "重庆" },
        { name: "河北" },
        { name: "山西" },
        { name: "辽宁" },
        { name: "吉林" },
        { name: "黑龙江" },
        { name: "江苏" },
        { name: "浙江" },
        { name: "安徽" },
        { name: "福建" },
        { name: "江西" },
        { name: "山东" },
        { name: "河南" },
        { name: "湖北" },
        { name: "湖南" },
        { name: "广东" },
        { name: "海南" },
        { name: "四川" },
        { name: "贵州" },
        { name: "云南" },
        { name: "陕西" },
        { name: "甘肃" },
        { name: "青海" },
        { name: "内蒙古" },
        { name: "广西" },
        { name: "西藏" },
        { name: "宁夏" },
        { name: "新疆维吾尔自治区" },
        { name: "香港" },
        { name: "澳门" }
      ],
      selectIndustry: [],
      selectArea: [],
      postToBack: {}, //传给后台的数据
      page: 1 //传给后台的页码
    };
  },

  components: {
    "flow-window": FlowWindow
  },
  beforeCreate() {
    that = this;
  },

  methods: {
    searchBtn() {
      this.$axios
        .get(`${this.$api.precise}/precise/`, {
          params: {
            industryid: this.postData[0],
            province: this.postData[1],
            page: 1
          }
        })
        .then(res => {
          console.log(res.data.enterprise_data);
          res.data.enterprise_data.map(item=>{
            console.log(item.mobile);
            // item.mobile = 11
            
          })

          this.resultList = res.data.enterprise_data;
          this.count = res.data.count;
        });
    },
    select(item) {
      let a = this.curIndex;
      this.curSelect[a][0] = item.name;
      this.isShowSelectList = false;
      this.postData[this.curIndex] = item.id ? item.id :item.name;
    },
    showTypeList(index, item) {
      this.isShowSelectList = true;
      this.$nextTick(() => {
        let li = this.$refs.li[index];
        let hiddenList = this.$refs.hiddenList;
        let domRect = li.getBoundingClientRect();

        hiddenList.setAttribute("data-before", "rotate(60deg)");

        hiddenList.style.top = domRect.bottom - 60 + "px"; // -60为页面离可视区的top距离
        this.curIndex = index; //保存当前选择的类型，以{{curSelect[index][0]}}可以用
        if (item === "行业") {
          this.curDatalist = this.industry;
        }
        if (item === "省份") {
          this.curDatalist = this.area;
        }
      });
    },

    nextPage() {},

    search() {
      if (!this.wordlistindustry || !this.wordlistarea) {
        return alert("请选择行业或省份");
      }
      Object.values(this.wordlistindustry).map(item => {
        this.postToBack["id"] = item.id;
      });
      Object.values(this.wordlistarea).map(item => {
        this.postToBack["province"] = item.name;
      });

      console.log(this.$api.precise);

      this.$axios
        .get(`${this.$api.precise}/precise/`, {
          params: {
            industryid: 1,
            province: "西藏",
            page: 1
          }
        })
        .then(res => {
          console.log(res.data);
          this.companyList = res.data.enterprise_data;
          this.count = res.data.count;
        });
    },
    isShow(flag) {
      this.flag = flag;
      this.isShowFlowWindow = true;
    },

    getMore() {
      console.log("aaaaaa");

      this.page++;
      this.$axios
        .get(`${this.$api.precise}/precise/`, {
          params: {
            industryid: 1,
            province: "西藏",
            page: this.page
          }
        })
        .then(res => {
          if (res.data.code === 1) {
            return alert("没有更多了");
          }
          this.resultList = res.data.enterprise_data;
          this.count = res.data.count;
        });
      // 下一批的旋转动画
      // this function will get more company's info
      if (this.canRotate) {
        this.newdeg += 360;
        let newobj = {};
        newobj.transform = `rotate(${this.newdeg}deg)`;
        this.deg = newobj;
        this.canRotate = false;
        setTimeout(() => {
          // 防抖
          this.canRotate = true;
        }, 2000);
      } else {
        return;
      }
    },

    capitalChoose() {
      let items = [
        "全部",
        "100万以下",
        "100~500万",
        "500~1000万",
        "1000~5000万",
        "5000万以上"
      ];
      let arr = [];
      function choosed() {
        console.log(this, that);
        that.capital = this.innerText;
        console.log(this.capital);
      }
      items.forEach(item => {
        arr.push({
          label: item,
          onClick: choosed
        });
      });

      this.$weui.actionSheet(arr, [
        {
          label: "取消",
          onClick: function() {
            console.log("取消");
          }
        }
      ]);
    },
    choosed() {
      this.exclude = this.innerText;
      console.log(this.exclude);
    },
    excludeChoose() {
      let items = [
        "不过滤",
        "过滤一周内已发送",
        "过滤一个月内已发送",
        "过滤已发送号码"
      ];
      let arr = [];

      items.forEach(item => {
        arr.push({
          label: item,
          onClick: this.choosed
        });
      });

      this.$weui.actionSheet(arr, [
        {
          label: "取消",
          onClick: function() {
            // console.log('取消');
          }
        }
      ]);
    },
    closeFlowWindowP() {
      this.isShowFlowWindow = false;
    },
    showwordlist(e) {
      var type = e.type;
      var data = e.data;

      if (type == "选择行业") {
        this.wordlistindustry = data;
      } else {
        this.wordlistarea = data;
      }
      this.isShowFlowWindow = false;
    }
  }

  // created() {
  //   // 设置域名
  //   this.$axios.defaults.baseURL = "https://easy-mock.com";
  // }
};
</script>

<style lang="stylus" scoped>
#precisionSearch
  width 96vw
  background-color #fff
  height 320px
  position absolute
  top 60px
  left 2vw
  border-radius 3px
  text-align center
  padding 50px 5px 0
  box-sizing border-box
  .magnifierBox
    position absolute
    width 55px
    height 55px
    margin 0 auto 
    border-radius 50%
    display flex
    justify-content center
    align-items center
    top -20px
    left 50%
    background-color #ffffff
    z-index 2
    transform translateX(-50%)
    .magnifierOut
      display inline-block
      width 45px
      height 45px
      background-color  #08a3a2
      border-radius 50%
      display flex
      justify-content center
      align-items center
      .magnifierInner
        display inline-block
        width 32px
        height 32px
        background url(../../../assets/icon/magnifier.png) center no-repeat 
        transform scale(0.8)

  .list
    width 100%
    .item
      position relative
      background-color #f3f3f3
      border-radius 3px
      margin-bottom 10px
      display flex
      justify-content space-between
      &::before
        content ''
        position absolute
        width 20px
        height 20px
        right 10px
        top 50%
        background url('../../../assets/icon/arrow.png') center no-repeat 
        transform translateY(-50%) rotate(-90deg) scale(0.8)
      .text
        display inline-block
        padding 10px 10px
        &.r
          padding 10px 30px
    &.sp
      position absolute
      top 200px
      height 320px
      overflow-y auto
      padding-bottom 10px
      background-color #f3f3f3
      .sp-i
        justify-content flex-end
        margin-bottom 0
        border-bottom 1px solid #ccc
        border-radius 0
        padding-right 20px
        &::before
           background url('')       
  .searchBtn
    position relative
    width 90%
    padding 12px 30px
    border-radius 20px
    background-color #0ebfc0
    font-size 16px
    color #fff
    box-shadow 0px 0px 4px #ccc
  .resultList
    width 100%
    height 200px
    background #fff
    margin-top 20px
    .top
      display flex
      justify-content space-between
      padding 0 10px
    .lista
      width 100%
      text-align left
      .itema
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    .more
      color #0badad  


</style>


